import React, { useEffect, useState } from 'react';
import Fade from 'react-reveal';
import endpoints from '../constants/endpoints';
import FallbackSpinner from './FallbackSpinner';

export default function About() {
  const [data, setData] = useState(null);

  useEffect(() => {
    fetch(endpoints.about)
      .then((res) => res.json())
      .then((res) => setData(res))
      .catch(() => {});
  }, []);

  if (!data) {
    return <FallbackSpinner />;
  }

  return (
    <Fade>
      <section className="about-section">
        <div className="about-grid">

          {/* LEFT COLUMN */}
          <div className="about-left">
            <h1>About Me</h1>

            <p>
              I’m Sinchana S Naik, Computer Science
              Engineering student from Yellapur, Karnataka.
            </p>

            <p>
              I am passionate about AI/ML, frontend development,
              automation, and building practical software solutions
              that solve real-world problems.
            </p>

            <p>
              I enjoy turning ideas into working applications and
              continuously learning modern technologies.
            </p>

            <p>
              My projects include Brain Tumor Detection using Swin
              Transformer &amp; Grad-CAM, a Blockchain Voting System,
              Automatic Waste Segregation System (ACCS finalist),
              and multiple AI &amp; embedded projects.
            </p>
          </div>

          {/* RIGHT COLUMN */}
          <div className="about-right">
            <h2>Education</h2>

            <div className="education-item">
              <h4>Bachelor of Engineering – CSE</h4>
              <p>P A College of Engineering, Mangalore (2022 – 2026)</p>
              <p>CGPA:8.8</p>
            </div>

            <div className="education-item">
              <h4>PUC</h4>
              <p>Jawahar Navodaya Vidyalaya, Malagi (2022 Passout)</p>
            </div>

            <div className="education-item">
              <h4>SSLC</h4>
              <p>Jawahar Navodaya Vidyalaya, Malagi (2020 Passout)</p>
            </div>
          </div>

        </div>
      </section>
    </Fade>
  );
}
